import React, { useState, useRef, useEffect } from 'react'
import { motion } from 'framer-motion'
import { FaTimes } from 'react-icons/fa'

interface StickyVideoPlayerProps {
  videoId: string
  playerRef?: React.MutableRefObject<any>
  onPlayerReady?: () => void
}

const StickyVideoPlayer: React.FC<StickyVideoPlayerProps> = ({ 
  videoId, 
  playerRef: externalPlayerRef,
  onPlayerReady 
}) => {
  const containerRef = useRef<HTMLDivElement>(null)
  const playerWrapperRef = useRef<HTMLDivElement>(null)
  const [isSticky, setIsSticky] = useState(false)
  const [isClosed, setIsClosed] = useState(false)
  const [containerTop, setContainerTop] = useState(0)

  useEffect(() => {
    if (isClosed) return

    // 초기 위치 저장
    if (containerRef.current && containerTop === 0) {
      const rect = containerRef.current.getBoundingClientRect()
      setContainerTop(rect.top + window.scrollY)
    }

    const handleScroll = () => {
      if (!containerRef.current) return

      const scrollY = window.scrollY
      const threshold = 150 // 플레이어가 화면 밖으로 나가는 임계값

      // 스크롤이 플레이어 원래 위치를 넘어가면 sticky 활성화
      if (scrollY > containerTop + threshold && !isSticky) {
        setIsSticky(true)
      }

      // 다시 원래 위치로 돌아오면 sticky 해제
      if (scrollY <= containerTop + threshold && isSticky) {
        setIsSticky(false)
      }
    }

    window.addEventListener('scroll', handleScroll, { passive: true })
    handleScroll() // 초기 체크

    return () => window.removeEventListener('scroll', handleScroll)
  }, [isSticky, containerTop, isClosed])

  // Sticky 모드 닫기
  const handleClose = () => {
    setIsClosed(true)
    setIsSticky(false)
  }

  return (
    <div ref={containerRef} className="relative">
      {/* Sticky 모드일 때 원래 자리 공간 유지 */}
      {isSticky && !isClosed && (
        <div className="aspect-video bg-gray-100 dark:bg-gray-800 rounded-xl flex items-center justify-center">
          <p className="text-gray-500 dark:text-gray-400 text-sm">
            영상이 상단에서 재생 중입니다
          </p>
        </div>
      )}

      {/* 플레이어 - Sticky 모드일 때 위치만 변경 */}
      <motion.div
        ref={playerWrapperRef}
        className={`${isSticky && !isClosed ? 'fixed z-50' : 'relative'}`}
        style={
          isSticky && !isClosed
            ? {
                top: '80px',
                left: window.innerWidth < 640 ? '20px' : '280px',
                right: window.innerWidth < 640 ? '20px' : 'auto',
                width: window.innerWidth < 640 ? 'calc(100vw - 40px)' : '400px',
                maxWidth: window.innerWidth >= 640 ? 'calc(100vw - 320px)' : '90vw',
              }
            : {}
        }
        animate={{
          scale: isSticky && !isClosed ? 1 : 1,
          opacity: 1,
        }}
        transition={{ type: 'spring', damping: 20, stiffness: 300 }}
      >
        <div
          className={`bg-black rounded-xl overflow-hidden shadow-2xl ${
            isSticky && !isClosed ? 'border-2 border-blue-500' : ''
          }`}
        >
          {/* Sticky 모드 헤더 */}
          {isSticky && !isClosed && (
            <div className="bg-gray-900 px-3 py-2 flex items-center justify-between">
              <span className="text-white text-xs font-medium flex items-center gap-2">
                <div className="w-2 h-2 rounded-full bg-red-500 animate-pulse"></div>
                재생 중
              </span>
              <button
                onClick={handleClose}
                className="p-1.5 hover:bg-red-600 rounded text-white transition-colors"
                title="닫기"
              >
                <FaTimes size={14} />
              </button>
            </div>
          )}

          {/* 플레이어 컨테이너 - 동일한 플레이어 유지 */}
          <div className="aspect-video bg-black relative">
            <div ref={externalPlayerRef} className="w-full h-full" />
          </div>
        </div>
      </motion.div>
    </div>
  )
}

export default StickyVideoPlayer

